import React from 'react'
import PropTypes from 'prop-types'
import styles from './ListItem.module.scss'



const ListItem = ({ name, image, description, price }) => {

    const ImageTag = image ? 'img' : 'div'

    return (
        <li className={styles.wrapper} >
            {image && <ImageTag src={image} alt={name} className={image ? styles.image : styles.imageNone} />}

            <div>
                <p className={styles.about}>Dogs Name: <span>{name}</span></p>
                <p className={styles.about}>Dogs Price: <span>{price}</span></p>
                <p className={styles.description}>About:<span> {description}</span></p>
            </div>
        </li>
    )

}


ListItem.propTypes = {
    name: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    image: PropTypes.string,
    price: PropTypes.number,


}

ListItem.defaultProps = {
    image: null,
    link: null,
}

export default ListItem;