import React from "react";
import AppContext from "../../../context";
import styles from "./Form.module.scss";
import Input from "../Input/Input";
import Button from "../Button/Button";
import Radio from "./FormRadio";

const types = {
	sell: "sell",
	adopt: "adopt",
};

const description = {
	sell: "beautiful dogs for sell",
	adopt: "Dogs to Adopt",
};

class Form extends React.Component {
	state = {
		type: "",
		name: "",
		image: "",
		price: "",
		description: "",
	};

	handleRadioButtonChange = (type) => {
		this.setState({
			type: type,
		});
	};

	handleInputChange = (e) => {
		this.setState({
			[e.target.name]: e.target.value,
		});

		console.log(`dogName: ${this.state.name}
        dogPhoto: ${this.state.image},
        price: ${this.state.price},
        descritpion: ${this.state.description}
        `);
	};

	render() {
		const { type } = this.state;
		return (
			<AppContext.Consumer>
				{(context) => (
					<div className={styles.wrapper}>
						<h2 className={styles.title}>
							Add {description[type]}
						</h2>
						<form
							autoComplete="off"
							onSubmit={(e) =>
								context.addItem(e, this.state)
							}
							className={styles.form}
						>
							<div className={styles.wrapperRadio}>
								<Radio
									id={types.sellDog}
									checked={type === types.sell}
									changeFn={() =>
										this.handleRadioButtonChange(
											types.sell
										)
									}
								>
									{" "}
									Sell
								</Radio>
								<Radio
									id={types.adoptDog}
									checked={type === types.adopt}
									changeFn={() =>
										this.handleRadioButtonChange(
											types.adopt
										)
									}
								>
									{" "}
									Adopt
								</Radio>
							</div>
							<Input
								onChange={this.handleInputChange}
								value={this.state.name}
								name="name"
								label="Dog Name"
								maxLength={30}
							/>
							<Input
								onChange={this.handleInputChange}
								value={this.state.image}
								name="image"
								label="Dog Photo"
							/>
							{type === types.sell ? (
								<Input
									onChange={this.handleInputChange}
									value={this.state.price}
									name="price"
									label="Price"
								/>
							) : null}
							<Input
								onChange={this.handleInputChange}
								value={this.state.description}
								name="description"
								label="Description"
								tag="textarea"
							/>
							<Button secondary>add new dog</Button>
						</form>
					</div>
				)}
			</AppContext.Consumer>
		);
	}
}

export default Form;
